fieldset {
  border-width: 1px;
  border-style: groove;
  border-color: #d9d9d9;
  border-image: initial;
}
legend {
  width: auto;
  margin: 0 0 5px 5px;
  font-size: 12px;
  border: 0;
  color: inherit;
}
.linkage-container {
  width: 720px;
  left: 50%;
  top: 100px;
  margin-left: -360px;
  background: #fff;
  border-radius: 6px;
}
.linkage-container .custom-select {
  width: 30%;
  margin-right: 20px;
}
.anticon-cross {
  position: absolute;
  right: 20px;
  top: 15px;
  cursor: pointer;
  font-size: 16px;
}
.main-container {
  float: left;
}
.main-container .target-table-lable {
  display: block;
  margin-bottom: 5px;
  margin-top: 12px;
}
.main-container input {
  border-radius: 3px;
}
.declareTable .declareTitle {
  font-weight: bold;
  font-size: 14px;
}
.declareTable p {
  margin-top: 5px;
}
.clearfix:before .clearfix:after {
  content: "";
  height: 0;
  display: block;
  clear: both;
}
.clearfix {
  zoom: 1;
}
.auto-switch-container {
  display: block;
}
.auto-number-lable {
  width: 50px;
  display: block;
  float: left;
}
.auto-number-lable-container {
  display: block;
  margin: 0 0 10px 5px;
}

.ant-checkbox-wrapper .check-box {
  vertical-align: middle;
}
.ant-checkbox-wrapper span {
  vertical-align: middle;
}